var React = require('react');
var ReactDOM = require('react-dom');
var createReactClass = require('create-react-class');
import FileInput from 'react-fine-uploader/file-input'
import FineUploaderTraditional from 'fine-uploader-wrappers'
import DropMenuBar from './components/MenuBar';
import CommandLineBox from './components/CommandLineBox';
const ace = require('brace')
require('brace/mode/javascript')
require('brace/mode/scad')
require('brace/theme/chrome')

function setUpEditor (divname) {
    if (divname === undefined) { divname = 'editor' }
    if (document.getElementById(divname) === null) return
  
    gEditor = ace.edit(divname)
    gEditor.setValue('Command: ', 1)
    gEditor.getSession().setMode('ace/mode/batchfile')
    // gEditor.setTheme("ace/theme/ambiance")
    // gEditor.setTheme("ace/theme/chaos")
    // gEditor.setTheme('ace/theme/chrome')
    // gEditor.setTheme("ace/theme/clouds")
    // gEditor.setTheme("ace/theme/cobalt")
    // gEditor.setTheme("ace/theme/dawn") // nice
    // gEditor.setTheme("ace/theme/dreamweaver")
    // gEditor.setTheme("ace/theme/eclipse")
    // gEditor.setTheme("ace/theme/github")
    // gEditor.setTheme("ace/theme/idle_fingers")
    // gEditor.setTheme("ace/theme/katzenmilch")
    // gEditor.setTheme("ace/theme/kr_theme")
    // gEditor.setTheme("ace/theme/kuroir")
    // gEditor.setTheme("ace/theme/merbivore")
    // gEditor.setTheme("ace/theme/mono_industrial")
    // gEditor.setTheme("ace/theme/monokai")
    // gEditor.setTheme("ace/theme/pastel_on_dark")
    // gEditor.setTheme("ace/theme/solarized_dark")
    // gEditor.setTheme("ace/theme/solarized_light")
    gEditor.setTheme("ace/theme/terminal")
    // gEditor.setTheme("ace/theme/textmate")
    // gEditor.setTheme("ace/theme/tomorrow")
    // gEditor.setTheme("ace/theme/tomorrow_night")
    // gEditor.setTheme("ace/theme/tomorrow_night_blue")
    // gEditor.setTheme("ace/theme/tomorrow_night_bright")
    // gEditor.setTheme("ace/theme/tomorrow_night_eighties")
    // gEditor.setTheme("ace/theme/twilight")
    // gEditor.setTheme("ace/theme/vibrant_ink")
    // gEditor.setTheme("ace/theme/xcode")
  
    function runExec(editor) {
      var src = editor.getValue()
      exec_command(src)
      // if (src.match(/^\/\/\!OpenSCAD/i)) {
      //   editor.getSession().setMode('ace/mode/scad')
      //   // FIXME test for the global function first
      //   src = openscadOpenJscadParser.parse(src)
      // } else {
      //   editor.getSession().setMode('ace/mode/javascript')
      // }
      // if (gProcessor !== null) {
      //   gProcessor.setJsCad(src)
      // }
    }
    
    function exec_command(commandwithparam) {
      var lines = commandwithparam.split('\n')
      for(var x = lines.length - 1; x >= 0; x --)
      {
        if(lines[x].replace(' ','') != '')
          break;
      }
      var commands = lines[x].split(': ')
      var params
      gEditor.insert('\n')
      switch(commands[0])
      {
        case 'Command':
          switch(commands[1])
          {
            case 'L': case 'l': case 'line': case 'Line': 
              currentCommand = 'line'
              gEditor.insert('Specify first point: ')
            break;
            case 'c': case 'C': case 'Circle': case 'circle':
              currentCommand = 'circle'
              gEditor.insert('Specify center point for circle: ')
            break;
            case 'A': case 'a': case 'Arc': case 'arc':
              currentCommand = 'arc'
              gEditor.insert('Specify center point of arc: ')
            break;
            case 'P': case 'p': case 'Point': case 'point':
              currentCommand = 'point'       
              gEditor.insert('Specify a point: ')     
            break;
            case 'Cube': case 'cube':
              currentCommand = 'cube'
              gEditor.insert('Specify center point of cube: ')
            break;
            case 'Sphere': case 'sphere':
              currentCommand = 'sphere'
              gEditor.insert('Specify center point of sphere: ')
            break;
            case 'Cylinder': case 'cylinder':
              currentCommand = 'cylinder'
              gEditor.insert('Specify start point of cylinder: ')
            break;
            case 'Text': case 'text':
              currentCommand = 'text'
              gEditor.insert('Specify first corner: ')
            break;
            case 'scale': case 'Scale':
              currentCommand = 'scale'
              gEditor.insert('Select object to scale: ')
            break;
            case 'rotate': case 'Rotate':
              currentCommand = 'rotate'
              gEditor.insert('Select object to rotate: ')
            break;
            case 'translate': case 'Translate':
              currentCommand = 'translate'
              gEditor.insert('Select object to move: ')
            break;
            case 'center': case 'Center': case 'centre': case 'Centre':
                currentCommand = 'center'
                gEditor.insert('Select object to center: ')
            break;
            case 'mirror': case 'Mirror':
                currentCommand = 'mirror'
                gEditor.insert('Select object to mirror: ')
            break;
            case 'union': case 'Union':
                currentCommand = 'union'
                gEditor.insert('Select first object to union: ')
            break;
            case 'intersect': case 'Intersect':
                currentCommand = 'intersect'
                gEditor.insert('Select first object to intersect: ')
            break;
            case 'difference': case 'Difference':
                currentCommand = 'difference'
                gEditor.insert('Select first object: ')
            break;
            case 'linear_extrude': case 'Linear_extrude':
                currentCommand = 'linear_extrude'
                gEditor.insert('Select object to extrude: ')
            break;
            case 'rotate_extrude': case 'Rotate_extrude':
                currentCommand = 'rotate_extrude'
                gEditor.insert('Select object to rotate extrude: ')
            break;
            default:
              gEditor.insert('Command: ')
            break;
          }
        break;
        case 'Specify first point':
          params = commands[1].split(',')
          first.set(params[0],0,params[1])
          gEditor.insert('Specify next point: ')
        break;
        case 'Specify next point':
          params = commands[1].split(',')
          next.set(params[0],0,params[1])
          if(currentCommand == 'line')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_line()
          }
        break;
        case 'Specify center point for circle':
          params = commands[1].split(',')
          center.set(params[0],0,params[1])
          gEditor.insert('Specify radius of circle: ')
        break;
        case 'Specify radius of circle':
          radius = commands[1]
          if(currentCommand == 'circle')
          {  
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_circle()
          }
        break;
        case 'Specify center point of arc':
          params = commands[1].split(',')
          center.set(params[0],0,params[1])
          gEditor.insert('Specify radius of arc: ')
        break;
        case 'Specify radius of arc':
          radius = commands[1]
          gEditor.insert('Specify start angle of arc: ')
        break;
        case 'Specify start angle of arc':
          startAngle = commands[1]
          gEditor.insert('Specify end angle of arc: ')
        break;
        case 'Specify end angle of arc':
          endAngle = commands[1]
          if(currentCommand == 'arc')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_arc()
          }
        break;
        case 'Specify a point':
            params = commands[1].split(',')
          center.set(params[0],0,params[1])
          if(currentCommand == 'point')
          {  
            draw_point()
            gEditor.insert('Command: ')
          }
        break;
        case 'Specify center point of cube':
            params = commands[1].split(',')
          center.set(params[0],0,params[1])
          gEditor.insert('Specify size of cube: ')
        break;
        case 'Specify size of cube':
            params = commands[1].split(',')
          width = params[0]
          height = params[1]
          depth = params[2]
          if(currentCommand == 'cube')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_cube()
          }
        break;
        case 'Specify center point of sphere':
        params = commands[1].split(',')
          center.set(params[0],0,params[1])
          gEditor.insert('Specify radius of sphere: ')
        break;
        case 'Specify radius of sphere':
          radius = commands[1]
          if(currentCommand == 'sphere')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_sphere()
          }
        break;
        case 'Specify start point of cylinder':
            params = commands[1].split(',')
          center.set(params[0],0,params[1])
          gEditor.insert('Specify radius of cylinder: ')
        //   gEditor.insert('Specify end point of cylinder: ')
        break;
        case 'Specify radius of cylinder':
            radius = commands[1]
            gEditor.insert('Specify height of cylinder: ')
        break;
        case 'Specify height of cylinder':
            height = commands[1]
            if(currentCommand == 'cylinder')
            {
                currentCommand = ''
                gEditor.insert('Command: ')
                draw_cylinder()
            }
        break;
        case 'Specify end point of cylinder':
            params = commands[1].split(',')
          next.set(params[0],0,params[1])
          gEditor.insert('Specify radius of cylinder: ')
        break;
        case 'Specify radius of cylinder':
          radius = commands[1]
          if(currentCommand == 'cylinder')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_cylinder()
          }
        break;
        case 'Specify first corner':  
            params = commands[1].split(',')
          first.set(params[0],params[1],params[2])
          gEditor.insert('Specify text: ')
        break;
        case 'Specify text':  
          text = commands[1]
          if(currentCommand == 'text')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            draw_text()
          }
        break;
        case 'Specify scale factor':
          radius = commands[1]
          if(currentCommand == 'scale')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            ops_scale()
          }
        break;
        case 'Specify rotation angle':
          endAngle = commands[1].split(',')
          if(currentCommand == 'rotate')
          {
            currentCommand = ''
            gEditor.insert('Command: ')
            ops_rotate()
          }
        break;
        case 'Specify translate factor':
            next = commands[1].split(',')
            if(currentCommand == 'translate')
            {
                currentCommand = ''
                gEditor.insert('Command: ')
                ops_translate()
            }
        break;
        case 'Specify axis to mirror':
            radius = commands[1]
            if(currentCommand == 'mirror')
            {
                currentCommand = ''
                gEditor.insert('Command: ')
                ops_mirror()
            }
        break;
        case 'Specify height to extrude':
            height = commands[1]
            if(currentCommand == 'linear_extrude')
            {
                currentCommand = ''
                gEditor.insert('Command: ')
                ops_linear_extrude()
            }
        break;
        default:
          gEditor.insert('Command: ')
        break;
      }
    }
    // enable special keystrokes
    gEditor.commands.addCommand({
      name: 'setJSCAD',
      // bindKey: { win: 'F5|Shift-Return', mac: 'F5|Shift-Return' },
      bindKey: { win: 'Return', mac: 'Return' },
      exec: runExec
    })
    gEditor.on("click", function(e) {
      gEditor.navigateFileEnd()
    })
    return gEditor
  }

const uploader = new FineUploaderTraditional({
    options: {
       request: {
          endpoint: 'upload/'
       },
       autoUpload: false
    },
});

var App = createReactClass({
    render: function () {
        setUpEditor()
        return (
            <div className="">
                <div className="btn-tool">
                    <button type="button" className="btn btn-default" id="2d-cad">
                        2D CAD
                    </button>
                </div>
                <div className="btn-tool">
                    <button type="button" className="btn btn-default active" id="3d-cad">
                        3D CAD
                    </button>
                </div>
                <div className="btn-tool">
                    <button type="button" className="btn btn-default" id="simulation">
                        SIMULATION
                    </button>
                </div>
                <div>
                    <DropMenuBar/>
                </div>
            </div>
        );
    }
});

ReactDOM.render(<App/>,  document.getElementById("app"));


