var React = require('react');
var createReactClass = require('create-react-class');

var CommandLineBox = createReactClass({
    render() {
        return (
            <div>
                <textarea type="text" placeholder="Command " className="cli-input"/>
            </div>
        );
    }
});

export default CommandLineBox;


