var React = require('react');
var createReactClass = require('create-react-class');
import Menu, {SubMenu, MenuItem, Divider} from 'rc-menu';
import 'rc-menu/assets/index.css';
import animate from 'css-animation';
import ace from'brace';

var DropMenuBar = createReactClass({
    onClickHandler(info) {
        let gEditor = ace.edit('editor')
        switch(info.key)
        {
            case "2-1":
                currentCommand = 'line'  
                gEditor.insert('Line\nSpecify first point: ')
                currentShape = 1;
                break;
            case "2-2":
                currentCommand = 'circle'  
                gEditor.insert('Circle\nSpecify center point for circle: ')
                currentShape = 2;
                break;
            case "2-3":
                currentCommand = 'arc'  
                gEditor.insert('Arc\nSpecify center point for arc: ')
                currentShape = 3;
                break;
            case "2-4":
                currentCommand = 'point'     
                gEditor.insert('Point\nSpecify a point: ')
                currentShape = 4;
                break;
            case "4-3":
                
                break;
        }
    },

    render() {
        return (
            <div>
                <Menu mode="horizontal" onClick={this.onClickHandler}>
                    <SubMenu title="FILE" key="1">
                        <MenuItem key="1-1">NEW</MenuItem>
                        <MenuItem key="1-2">OPEN</MenuItem>
                        <MenuItem key="1-3">SAVE</MenuItem>
                        <MenuItem key="1-4">SAVE_AS</MenuItem>
                    </SubMenu>
                    <SubMenu title="DRAW" key="2">
                        <MenuItem key="2-1">LINE</MenuItem>
                        <MenuItem key="2-2">CIRCLE</MenuItem>
                        <MenuItem key="2-3">ARC</MenuItem>
                        <MenuItem key="2-4">POINT</MenuItem>
                    </SubMenu>
                    <SubMenu title="EDIT" key="3">
                        <MenuItem key="3-1">DELETE</MenuItem>
                        <MenuItem key="3-2">EXTEND</MenuItem>
                        <MenuItem key="3-3">FILLET</MenuItem>
                        <MenuItem key="3-4">MEASURE</MenuItem>
                    </SubMenu>
                    <SubMenu title="SURFACE" key="4">
                        <MenuItem key="4-1">BOUNDARY</MenuItem>
                        <MenuItem key="4-2">CREATE</MenuItem>
                        <MenuItem key="4-3">AXIS</MenuItem>
                        <MenuItem key="4-4">SAVE_AS</MenuItem>
                    </SubMenu>
                </Menu>
            </div>
        );
    }
});

export default DropMenuBar;


